export const handleFile = (ctx, context, action, layers, setAction) => {

    const height = ctx.canvas.height;
    const width = ctx.canvas.width;

    //Detects if the upload action has been removed, and if so, 
    // removes the input so to prevent upload dialog from reactivating
    function checkCancelledUpload(input) {
        document.body.onfocus = () => {
            if (input.value.length > 0) {
                document.body.onfocus = null;
            } else {
                input.remove();
                setAction(false);
                document.body.onfocus = null;
            }
        }
    }

    const saveFile = () => {
        let finalCanvas = document.createElement("canvas");
        let finalCtx = finalCanvas.getContext("2d");
        finalCanvas.width = width;
        finalCanvas.height = height;
        function constructFilters(filters) {
            if (!filters) return ""
            let filtersString = "";
            Object.keys(filters).forEach((key) => {
                if (key === "hue-rotate") {
                    filtersString += `${key}(${filters[key]}deg) `
                } else if (key === "blur") {
                    filtersString += `${key}(${filters[key]}px) `
                } else if (key === "drop-shadow") {
                    filtersString += `${key}(${filters[key][0]}px ${filters[key][1]}px ${filters[key][2]}px ${filters[key][3]}) `
                } else {
                    filtersString += `${key}(${filters[key]}%) `
                };
            })
            return filtersString
        }
        layers.forEach((layer) => {
            if (layer.visible) {
                finalCtx.globalCompositeOperation = layer.blending;
                finalCtx.globalAlpha = layer.opacity;
                if (layer.filters) {
                    finalCtx.filter = constructFilters(layer.filters)
                }
                finalCtx.drawImage(
                    document.getElementById(`drawCanvasLayer${layer.id}`),
                    0,
                    0
                );
            }
        })
        let downloadLink = document.createElement("a");
        downloadLink.setAttribute("download", context.name + ".png");
        let dataURL = finalCanvas.getContext("2d").canvas.toDataURL("image/png");
        let url = dataURL.replace(
            /^data:image\/png/,
            "data:application/octet-stream",
        );
        downloadLink.setAttribute("href", url);
        downloadLink.click();
    };
    const newFile = () => {
        const baseCanvas = document.getElementById(`drawCanvasLayer0`).getContext("2d");
        if (context.useAlpha) {
            baseCanvas.fillStyle = "#00000000";
        } else {
            baseCanvas.fillStyle = "#FFFFFFFF";
        }
        baseCanvas.fillRect(0, 0, width, height);
    };

    const openFile = () => {
        const input = document.createElement("input");
        input.type = "file";
        input.accept = "image/*";
        input.onchange = (e) => {
            const file = e.target.files[0];
            const reader = new FileReader();
            reader.onload = () => {
                const image = new Image();
                image.src = reader.result;
                image.onload = () => {
                    const baseCanvas = document.getElementById(`drawCanvasLayer0`);
                    baseCanvas.width = image.width;
                    baseCanvas.height = image.height;
                    context.setDimentions({ width: image.width, height: image.height });
                    context.setName(file.name);
                    const baseCtx = baseCanvas.getContext("2d");
                    baseCtx.drawImage(image, 0, 0);
                    setAction("Open Confirm");
                    input.remove();
                };
            };
            reader.readAsDataURL(file);
        };
        input.click();
        checkCancelledUpload(input);
    }

    const uploadImage = () => {
        const input = document.createElement("input");
        input.type = "file";
        input.accept = "image/*";
        input.onchange = (e) => {
            const file = e.target.files[0];
            const reader = new FileReader();
            reader.onload = () => {
                const image = new Image();
                image.src = reader.result;
                image.onload = () => {
                    const tempCanvas = document.createElement("canvas");
                    tempCanvas.width = image.width;
                    tempCanvas.height = image.height;
                    const tempCtx = tempCanvas.getContext("2d");
                    tempCtx.drawImage(image, 0, 0);
                    const data = tempCtx.getImageData(0, 0, image.width, image.height);
                    context.setClipboard({ data: data, state: "carry" });
                    context.setTool("Select");
                    setAction("clipping");
                    input.remove();
                };
            };
            reader.readAsDataURL(file);
        };
        input.click();
        checkCancelledUpload(input);
    }

    switch (action) {
        case "new":
            newFile();
            break;
        case "save":
            saveFile(ctx);
            break;
        case "open":
            openFile(ctx);
            break;
        case "image":
            uploadImage(ctx);
            break;
        default:
            break;
    }
}